.footer{
  background-image: url(../img/footer-bg.webp);
  background-size: cover;
  background-position: center;
  padding: 5.35em 0;
  @media screen and (min-width: 577px){
    .container{
      padding: 0 23em;
    }
  }
  &__inner{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__logo{
    width: 21.0625em;
    display: block;
    img{
      width: 100%;
      display: block;
    }
  }
  &__socials,
  &__links{
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  &__link{
    font-size: .875em;
    line-height: 130%;
    color: #fff;
  }
  &__socials{
    align-items: flex-start;
    &-item{
      color: #fff;
      display: flex;
      align-items: center;
      @include lastM(0 0 .5em);
      img{
        width: 2.1875em;
        flex-shrink: 0;
        margin-right: 1em;
      }
      span{
        font-size: 1.25em;
        line-height: 125%;
        text-decoration: underline;
      }
    }
  }
}


@media screen and (max-width: 576px){
  .footer{
    padding: 1.25em 0 .94em;
    background-image: url(../img/footer-bg2.webp);
    &__inner{
      flex-direction: column;
    }
    &__logo{
      width: 11.57281em;
    }
    &__links{
      margin-top: 1.62em;
    }
    &__link{
      font-size: .8125em;
    }
    &__socials{
      margin-top: 2.62em;
      &-item{
        span{
          font-size: 1em;
        }
        img{
          margin-right: .75em;
        }
      }
    }
  }
}
