@import 'vars';
html {
    width: 100%;
    &.fixed-body {
        overflow-y: hidden;
    }
}

.wrapper {
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    flex-shrink: 0;
}

.content {
    flex-grow: 1;
    display: flex;
    flex-direction: column;
    width: 100%;
    overflow: hidden;
}

.m-page {
    width: calc(100vw - (100vw - 100%));
    overflow-x: hidden;
    font-family: $font1;
    font-weight: 500;
    color: $black;
    font-size: calc(100vw * (16 / 1920));
    //@media screen and (max-width: 1264px){
    //    font-size: calc(100vw * (16 / 1264));
    //}
    &.fixed-body,
    &.fixed-body2{
        overflow-y: hidden;
        padding-right: var(--scrollWidth);
        .header{
            padding-right: var(--scrollWidth);
        }
    }
}

.container {
    width: 100%;
    margin: 0 auto;
    padding: 0 15.625em;
    &._full{
        max-width: 100%;
    }
}

a {
    &:hover {
        color: $primary;
    }
}


@media screen and (max-width: 992px){
    .m-page{
        font-size: calc(100vw * (16 / 768) * .5);
    }

    .container{
        padding: 0 5.25em;
    }
}


@media screen and (max-width: 576px){
    .m-page{
        font-size: calc(100vw * (16 / 320));
    }

    .container{
        padding: 0 1.25em;
    }
}
