.modal {
    background-image: url(../img/modal-bg.webp);
    background-size: cover;
    background-position: center;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: $vh;
    overflow-y: auto;
    background-color: #fff;
    transition: all .3s;
    opacity: 0;
    pointer-events: none;
    padding: 7.2em 1.5em;
    z-index: 30;
    &._active{
        opacity: 1;
        pointer-events: unset;
    }
    &__close{
        z-index: 3;
        position: fixed;
        width: 2.5em;
        top: 8.44em;
        right: 32.81em;
        @include center;
        background: #FFF;
        border-radius: 1.5em;
        &:hover{
            path{
                fill: $primary;
            }
        }
        svg{
            width: 100%;
            height: auto;
        }
    }
    &__dialog{
        width: 32em;
        max-width: 100%;
        position: relative;
        z-index: 2;
        margin: 0 auto;
    }
    &__title{
        text-align: center;
    }
}

.modal-form{
    width: 24.1875em;
    max-width: 100%;
    margin: 1.13em auto 0;
    @include grid100gap(.75em);
    &__hint{
        margin-top: 1.25em;
        text-align: center;
        span{
            font-size: .625em;
            line-height: 130%;
            text-transform: uppercase;
        }
    }
    &__btn{
        width: 100%;
        margin-top: .7em;
        img{
            right: 2.44em;
        }
    }
}


@media screen and (max-width: 576px){
    .modal{
        background-image: url(../img/modal-bg2.webp);
        padding: 4.625em 2em;
    }

    .modal-form{
        &__hint{
            margin-top: .75em;
        }
    }
}
