.have-bought{
  padding: 9em 0 4.5em;
  position: relative;
  &__content{
    max-width: 43.75em;
    position: relative;
    z-index: 2;
    .text24{
      margin-top: 1em;
    }
  }
  &__nav{
    display: flex;
    margin-top: 2.69em;
  }
  &__img{
    position: absolute;
    top: -9em;
    right: 0;
    height: 52.3125em;
    img{
      height: 100%;
      width: auto;
      display: block;
    }
    ._view2{
      display: none;
    }
  }
  ._ellipse1{
    background: #DFC9EF;
    top: 7em;
    left: -10.5em;
  }
  ._img1{
    position: absolute;
    left: 0;
    bottom: 0;
    width: 10.875em;
  }
}

.feed{
  pointer-events: none;
  margin-top: -5.4em;
  img{
    width: 100%;
    display: block;
  }
  ._view2{
    display: none;
  }
}

.catalog{
  padding: 4.5em 0 0;
  position: relative;
  &__info{
    margin-top: 3em;
    display: flex;
    align-items: center;
    .text16{
      margin-left: .94em;
      color: #A8A8A8;
    }
  }
  &__grid{
    margin-top: 1.875em;
    display: grid;
    grid-template-columns: repeat(5,calc(20% - 2.19em * 5 / 4));
    gap: 2.44em 2.19em;
  }
  &__nav{
    margin-top: 2.94em;
    display: flex;
    justify-content: center;
    .m-btn{
      text-align: center;
      padding: 0 7.31em 0 5em;
    }
  }
  &__bg{
    position: absolute;
    pointer-events: none;
  }
  ._img1{
    left: 0;
    bottom: 5.625em;
    width: 10.75em;
  }
  ._img2{
    right: 0;
    top: 11.12em;
    width: 13.75em;
    filter: blur(6px) drop-shadow(1.25em 1.875em 2.5em rgba(0, 0, 0, 0.15));
  }
  ._img3{
    right: 1.96em;
    bottom: 10.11em;
    width: 17.75em;
  }
}

.filter{
  margin-top: 1.94em;
  display: flex;
  align-items: center;
  &__item{
    transition: all .3s;
    cursor: pointer;
    border-radius: 0.5em;
    background: #FFF;
    box-shadow: 0 .625em .9375em 0 rgba(0, 0, 0, 0.15);
    height: 1.75em;
    display: flex;
    align-items: center;
    padding: 0 .625em;
    @include lastM(0 .625em 0 0);
    &:hover{
      background-color: rgba($primary,.2);
    }
    svg{
      width: 1em;
      height: auto;
      margin-right: .625em;
    }
    span{
      font-size: .875em;
      line-height: 120%;
    }
    ._arrow{
      display: none;
      transition: all .3s;
    }
    &._active{
      ._arrow{
        display: block;
      }
    }
    &._reverse{
      ._arrow{
        transform: rotate(-180deg);
      }
    }
    &._disabled{
      pointer-events: none;
      opacity: .7;
    }
  }
}

.product-card{
  display: block;
  &:hover{
    color: $black;
  }
  &__img{
    @include imgCover(134.4%);
  }
  &__prices{
    margin-top: 1.25em;
    padding: 0 .25em;
    display: flex;
    align-items: flex-end;
  }
  &__price2{
    margin-left: .44em;
    color: #A4A4A4;
    text-decoration: line-through;
    font-weight: 400;
  }
  &__text{
    margin-top: .44em;
    white-space: nowrap;
    text-overflow: ellipsis;
    overflow: hidden;
    color: #949494;
    font-weight: 400;
    &:before{
      content: attr(data-name);
      color: $black;
      font-size: 1.14em;
      font-weight: 500;
    }
  }
  &__feedback{
    margin-top: .44em;
    display: flex;
    align-items: center;
    &-count{
      font-weight: 400;
    }
  }
  &__rating{
    display: flex;
    align-items: center;
    margin-right: .625em;
    img{
      margin-right: .625em;
      width: .93em;
      flex-shrink: 0;
    }
  }
  &__delivery{
    font-weight: 400;
    margin-top: .875em;
    display: flex;
    align-items: center;
    &-time{
      margin-left: .8em;
    }
  }
}

.lottery{
  position: relative;
  padding: 2.45em 0;
  .text60{
    text-align: center;
    line-height: 90%;
    position: relative;
    z-index: 2;
  }
  &__img{
    img{
      width: 100%;
      display: block;
    }
    ._view2{
      display: none;
    }
  }
  &__content{
    position: relative;
    margin-top: -1.1875em;
    margin-left: 16.87em;
    width: 69.778em;
  }
  &__nav{
    position: absolute;
    bottom: 3.94em;
    left: 16.06em;
  }
  &__text{
    position: relative;
    z-index: 2;
    margin: -.815em auto 0;
    width: 48em;
    max-width: 100%;
    text-align: center;
  }
  &__bg{
    position: absolute;
    pointer-events: none;
  }
  ._img1{
    left: 0;
    top: 8.64em;
    width: 35.125em;
  }
  ._img2{
    right: 0;
    top: -2em;
    width: 41.375em;
  }
  ._img3{
    top: -.875em;
    left: 0;
    width: 100%;
  }
}

.faq{
  padding: 2.81em 0 5.5em;
  position: relative;
  .container{
    position: relative;
    z-index: 2;
  }
  &__items{
    margin-top: 2.69em;
    max-width: 50.125em;
    @include grid100gap(.94em);
  }
  &__bg{
    position: absolute;
    pointer-events: none;
  }
  ._img1{
    left: 0;
    top: .4em;
    width: 47.75em;
  }
  ._img2{
    right: 0;
    top: 4.56em;
    width: 54.3125em;
  }
  ._img3{
    left: 0;
    top: -2.81em;
    width: 100%;
  }
}

.qa-card{
  &._active{
    .qa-card__btn{
      transform: rotate(-180deg);
    }
  }
  &__show{
    background-color: #fff;
    cursor: pointer;
    padding: .94em 1.875em;
    border-radius: 6.25em;
    border: 1px dashed #F2BD35;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__btn{
    width: 1.375em;
    margin-left: 1.25em;
    flex-shrink: 0;
    img{
      width: 100%;
      display: block;
      transition: all .3s;
    }
  }
  &__hidden{
    margin-top: .75em;
    display: none;
  }
}


@media screen and (max-width: 576px){
  .have-bought{
    padding: .9em 0 1em;
    &__img{
      margin-top: -.25em;
      width: 100%;
      height: auto;
      position: static;
      transform: unset !important;
      img{
        width: 100%;
        height: auto;
      }
      ._view1{
        display: none;
      }
      ._view2{
        display: block;
      }
    }
    .main-ellipse{
      display: none;
    }
    .text24{
      margin-top: .25em;
    }
    &__nav{
      margin-top: 1.25em;
    }
  }

  .feed{
    margin-top: 0;
    ._view1{
      display: none;
    }
    ._view2{
      display: block;
    }
  }

  .catalog{
    padding: 1em 0 0;
    &__info{
      margin-top: .625em;
    }
    &__grid{
      grid-template-columns: repeat(2,calc(50% - .625em));
      gap: 1.25em;
      margin-top: 1.875em;
    }
    &__nav{
      margin-top: 1.88em;
      .m-btn{
        padding: 0 2.5em;
        img{
          display: none;
        }
      }
    }
  }

  .filter{
    margin-top: .1em;
    flex-wrap: wrap;
    width: calc(100% + .5625em);
    &__item{
      padding: 0 .625em;
      margin: .5625em .5625em 0 0 !important;
      span{
        font-size: .75em;
      }
      ._arrow{
        width: .75em;
        margin-right: .25em;
      }
    }
  }

  .product-card{
    &__prices{
      margin-top: .625em;
    }
    &__price1{
      font-size: 1em;
    }
    &__price2{
      font-size: .875em;
    }
    &__text{
      font-size: .75em;
      margin-top: .41em;
      padding-top: 1.25em;
      position: relative;
      &:before{
        position: absolute;
        top: 0;
        left: 0;
        font-size: 1.08em;
      }
    }
    &__feedback{
      margin-top: .3125em;
      &-count{
        font-size: .75em;
        white-space: nowrap;
      }
    }
    &__rating{
      margin-right: .31em;
      img{
        width: .67em;
        margin-right: .31em;
      }
    }
    &__delivery{
      margin-top: .31em;
      .text14{
        font-size: .75em;
      }
      &-time{
        margin-left: .42em;
      }
    }
  }

  .lottery{
    padding: 4em 0 3.44em;
    .text60{
      line-height: 125%;
    }
    &__content{
      margin-top: 0;
      margin-left: 0;
      width: 100%;
    }
    &__img{
      width: calc(100% + 2.5em);
      margin-left: -1.25em;
      ._view1{
        display: none;
      }
      ._view2{
        display: block;
      }
    }
    &__nav{
      width: 100%;
      left: 0;
      bottom: 0;
    }
    &__text{
      margin-top: 1.56em;
    }
  }

  .faq{
    padding: 3.44em 0 2.44em;
    &__items{
      gap: .87em;
      margin-top: .94em;
    }
  }

  .qa-card{
    &__show{
      padding: .63em 1.25em;
    }
    &__btn{
      width: 1em;
    }
  }
}
