.main{
  min-height: 49.3125em;
  padding: 18.38em 0 7.53em;
  position: relative;
  .container{
    position: relative;
    z-index: 2;
  }
  &__text{
    .text60{
      margin-top: -.4em;
    }
  }
  &__steps{
    margin-top: 11.5em;
    display: grid;
    grid-template-columns: repeat(4,calc(25% - 3.75em / 4));
    gap: 1.25em;
  }
  ._img1{
    top: 0;
    left: 0;
    width: 30.625em;
    z-index: 2;
  }
  ._img2{
    top: 32.25em;
    left: -5.2em;
    width: 13.125em;
    z-index: 2;
  }
  ._img3{
    z-index: 2;
    top: 21.06em;
    left: 3.62em;
    width: 10.8125em;
    filter: drop-shadow(0 5em 2.5em rgba(0, 0, 0, 0.15));
  }
  ._img4{
    z-index: 4;
    top: -4.625em;
    left: 43.18em;
    width: 20.625em;
    filter: drop-shadow(0 5em 2.5em rgba(0, 0, 0, 0.15));
  }
  ._img5{
    top: 3.25em;
    left: 65em;
    width: 7.1875em;
    filter: blur(5px);
  }
  ._img6{
    top: 10.56em;
    left: 59.69em;
    width: 12em;
    filter: drop-shadow(0 5em 2.5em rgba(0, 0, 0, 0.15));
  }
  ._img7{
    top: 14.75em;
    right: 40.73em;
    width: 7.1875em;
    filter: blur(2px);
    z-index: 2;
  }
  ._img8{
    top: 13.875em;
    right: 15.5625em;
    width: 20.8125em;
  }
  ._img9{
    top: -1.62em;
    right: 4.84em;
    width: 7.625em;
    z-index: 3;
  }
  ._img10{
    top: 3.88em;
    right: 0;
    width: 12.1875em;
    z-index: 3;
  }
  ._img11{
    top: 21.12em;
    right: 0;
    width: 7.75em;
    z-index: 3;
  }
  ._img12{
    top: 0;
    right: 0;
    width: 21.875em;
    z-index: 2;
  }
  ._img13{
    top: 0;
    left: 25.87em;
    width: 58.25em;
  }
  ._img14{
    top: 29.85em;
    left: 0;
    width: 79.5em;
  }
  .rect{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 49.3125em;
    z-index: -1;
    background: linear-gradient(180deg, rgba(255, 255, 255, 0.00) 80.85%, #FFF 100%), #EFEDEE;
  }
}

.parallax-item{
  position: absolute;
  transition: 0.135s ease-in-out;
}

.main-ellipse{
  position: absolute;
  filter: blur(100px);
  width: 39.75em;
  height: 24.4375em;
  border-radius: 39.75em;
  &._ellipse1{
    background: rgba(182, 169, 163, 0.50);
    top: -9.69em;
    left: -5.12em;
  }
  &._ellipse2{
    top: -15.625em;
    left: 57.44em;
    background: rgba(255, 205, 131, 0.40);
  }
  &._ellipse3{
    right: -22.19em;
    top: 36.25em;
    background: rgba(255, 205, 131, 0.40);
  }
  &._ellipse4{
    left: 38.62em;
    top: 29.19em;
    background: #DFC9EF;
  }
}

.step-card{
  position: relative;
  &__img{
    width: 100%;
    display: block;
  }
  &__content{
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    max-height: 100%;
    overflow: hidden;
  }
  &__top{
    padding: .94em 1.62em;
    display: flex;
    align-items: center;
    font-weight: 900;
  }
  &__num{
    margin-right: 3em;
    color: #fff;
  }
  &__desc{
    margin-top: 1.44em;
    padding: 0 1.81em;
    max-width: 15em;
  }
  &__wb{
    position: absolute;
    right: .25em;
    bottom: .625em;
    width: 10.4375em;
    z-index: 2;
    filter: drop-shadow(0 1.875em 2.5em rgba(127, 56, 126, 0.30));
  }
  &__stars{
    position: absolute;
    bottom: -2.19em;
    left: 1.88em;
    width: 12.375em;
  }
  &__tg{
    position: absolute;
    bottom: -1.38em;
    left: 6.44em;
    width: 8.5em;
  }
  &__gift{
    position: absolute;
    top: -4.31em;
    right: -3.9em;
    width: 9em;
  }
  &__gift2{
    position: absolute;
    top: 4.44em;
    right: 5.05em;
    width: 4.5625em;
  }
  &__gift3{
    position: absolute;
    bottom: -1.38em;
    right: 6.25em;
    width: 5.5625em;
  }
}

.winners{
  margin-top: 6em;
  position: relative;
  &__inner{
    display: grid;
    grid-template-columns: 26.5em calc(100% - 26.5em - 1.84em);
    gap: 1.84em;
    align-items: center;
    position: relative;
    z-index: 3;
  }
  &__img{
    img{
      position: static;
      width: 100%;
      display: block;
    }
  }
  &__desc{
    margin-top: 1.31em;
    max-width: 43.75em;
  }
  &__nav{
    display: flex;
    margin-top: 3.31em;
  }
  &__bg{
    position: absolute;
  }
  ._img1{
    top: -1.06em;
    width: 25.8125em;
  }
  ._img2{
    left: 0;
    top: 10em;
    width: 8.875em;
  }
  ._img3{
    right: 0;
    top: 11.81em;
    left: unset;
    width: 58.4375em;
    z-index: unset;
  }
}

.winners-ellipse{
  width: 28.375em;
  height: 17.4375em;
  border-radius: 28.375em;
  background: #DFC9EF;
  filter: blur(6.25em);
  position: absolute;
  right: 0;
  top: 9.75em;
  z-index: -1;
}

.prizes{
  padding: 7.53em 0 9em;
  position: relative;
  &__inner{
    box-shadow: 0 1.25em 3.125em 0 rgba(0, 0, 0, 0.15);
    background-image: url(../img/prize-bg2.webp);
    background-size: cover;
    background-position: center;
    padding: 3.56em 3.75em;
    min-height: 40.4375em;
    border-radius: 6.25em;
    position: relative;
  }
  &__content{
    max-width: 29.1875em;
    @include grid100gap(.625em);
    .text64{
      text-transform: uppercase;
      line-height: 90%;
    }
  }
  &__img{
    right: -5.44em;
    top: -10.12em;
    width: 62.75em;
    position: absolute;
    img{
      display: block;
      width: 100%;
    }
  }
  &__bg{
    position: absolute;
  }
  ._img1{
    left: 19.25em;
    bottom: 5.25em;
    width: 14.5625em;
  }
  ._img2{
    left: 28.06em;
    bottom: 4.25em;
    width: 8.9375em;
  }
  ._img3{
    top: 1.5em;
    left: 0;
    width: 33.875em;
    z-index: -1;
  }
  ._img4{
    right: 0;
    top: 0;
    z-index: -1;
    width: 28.375em;
  }
}


@media screen and (min-width: 577px){
  ._hide-desktop{
    display: none;
  }
}


@media screen and (max-width: 992px){
  .main{
    @media screen and (min-width: 577px){
      &__text{
        padding-left: 10em;
      }
    }
  }
}


@media screen and (max-width: 576px){
  ._hide-mob{
    display: none;
  }

  .main{
    padding: 9.19em 0 .69em;
    &__mob{
      position: absolute;
      width: 100%;
      display: block;
      top: 0;
      left: 0;
    }
    &__text{
      text-align: center;
      .text60{
        margin: 0;
        line-height: 100%;
      }
    }
    &__steps{
      margin-top: 5.63em;
      grid-template-columns: 100%;
      gap: 1.5em;
    }
  }

  .step-card{
    &__top{
      padding: .44em 1.38em;
      align-items: center;
    }
    &__num{
      margin-right: 2.19em;
      .text36{
        font-size: 2.25em;
      }
    }
    &__desc{
      margin-top: .81em;
      padding: 0 1.44em;
      max-width: 13em;
    }
    &__wb{
      width: 6.9375em;
      right: .81em;
      bottom: .5em;
    }
    &__stars{
      bottom: -1.31em;
      width: 5.625em;
      left: 6.44em;
      z-index: 2;
    }
    &__tg{
      width: 4.125em;
      left: 4.69em;
      bottom: -1.31em;
    }
    &__gift3{
      width: 4.25em;
      bottom: -2.37em;
      right: 4.75em;
    }
  }

  .winners{
    margin-top: 3.75em;
    &__inner{
      grid-template-columns: 100%;
    }
    &__img{
      display: none;
    }
    &__desc{
      margin-top: .625em;
    }
    &__nav{
      margin-top: 1.875em;
    }
    ._img2{
      top: -.81em;
      left: unset;
      right: 0;
      width: 5.125em;
    }
    ._img3{
      width: 100%;
      top: 12.81em;
    }
  }

  .prizes{
    padding: .69em 0 .9em;
    &__inner{
      padding: 2.13em 1.25em;
      min-height: 39.75rem;
      border-radius: 1.5625em;
      background-image: url(../img/prize-bg3.webp);
    }
    &__content{
      gap: 0;
    }
    &__img{
      width: 24.9175em;
      left: 50%;
      right: unset;
      top: unset;
      bottom: -.38em;
      transform: translateX(-50%) !important;
    }
    ._img1{
      bottom: 2em;
      left: 2.38em;
      width: 6.25em;
    }
    ._img2{
      bottom: 1.5em;
      left: 6.19em;
      width: 3.8125em;
    }
    ._img3{
      height: 16.875em;
      width: auto;
      top: -3em;
      left: -6.38em;
      z-index: 2;
    }
    ._img4{
      top: 7em;
      width: 8.75em;
      z-index: 2;
    }
  }
}

