.logo{
  flex-shrink: 0;
  display: block;
  img{
    width: 100%;
    display: block;
  }
}

.header{
  z-index: 5;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  &__inner{
    padding: 1.06em 0;
    display: flex;
    justify-content: flex-end;
  }
  &__logo{
    width: 21.0625em;
  }
  &._open{
    .menu{
      transform: translateX(0);
    }
  }
}

.burger{
  position: absolute;
  left: 2.5em;
  top: 50%;
  transform: translateY(-50%);
  width: 3.75em;
  height: 3.75em;
  border-radius: 0.9375em;
  background-color: #fff;
  @include bgImgSize(0);
  box-shadow: .625em .625em 1.875em 0 rgba(0, 0, 0, 0.15);
  @include center;
  transition: all .3s;
  &:before{
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    border-radius: inherit;
    background: $gradient;
    transition: inherit;
    opacity: 0;
  }
  &:hover:before{
    opacity: 1;
  }
  svg{
    width: 1.5em;
    height: auto;
    position: relative;
    z-index: 2;
  }
}

.menu{
  transition: all .3s;
  transform: translateX(-120%);
  position: fixed;
  top: 0;
  left: 0;
  overflow: hidden;
  width: 33.5em;
  height: $vh;
  background-color: #fff;
  z-index: 10;
  box-shadow: 1.25em 0 2.5em 0 rgba(0, 0, 0, 0.25);
  &:before{
    content: '';
    position: absolute;
    width: 39.75em;
    height: 41.4375em;
    border-radius: 41.4375em;
    background: rgba(223, 201, 239, 0.75);
    filter: blur(6.25em);
    top: -13.63em;
    left: -22.63em;
  }
  &__img{
    position: absolute;
  }
  ._img1{
    width: 24.5em;
    top: 7.06em;
    right: 0;
  }
  ._img2{
    width: 12.25em;
    bottom: 10.78em;
    right: 0;
  }
  ._img3{
    bottom: 3.87em;
    right: 0;
    width: 6.5em;
  }
  ._img4{
    width: 100%;
    left: 0;
    bottom: 0;
  }
  &__inner{
    padding: 1.56em 1.94em 4em 4em;
    position: relative;
    z-index: 2;
    height: 100%;
    overflow-y: auto;
  }
  &__top{
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  &__logo{
    width: 12.4375em;
  }
  &__close{
    width: 2.5em;
    flex-shrink: 0;
    &:hover{
      img{
        transform: scale(1.05);
      }
    }
    img{
      transition: all .3s;
      width: 100%;
      display: block;
    }
  }
  &__links{
    margin-top: 1.31em;
    max-width: 24.9375em;
  }
  &__link{
    width: 100%;
    display: flex;
    padding: 2.5em 0;
    border-bottom: 1px dashed #CA8DC9;
    &:last-child{
      border-bottom: none;
    }
    span{
      font-size: 1.25em;
      line-height: 125%;
    }
  }
}


@media screen and (max-width: 992px){
  .header{
    &__inner{
      padding: .5em 0;
    }
    &__logo{
      width: 11.0625em;
    }
  }

  .burger{
    left: 1.25em;
    width: 2.5em;
    height: 2.5em;
    border-radius: 0.625em;
  }

  .menu{
    width: 100%;
    &__inner{
      padding: 1.56em 1.25em;
    }
    &__close{
      width: 1.875em;
    }
    &__links{
      margin-top: .625em;
    }
    &__link{
      padding: 1.25em 0;
    }
    ._img1{
      top: 4em;
      right: -1.5em;
      width: 12.1875em;
    }
    ._img2{
      width: 3.25em;
      bottom: 12.47em;
    }
    ._img3{
      width: 2em;
      bottom: 9.25em;
    }
  }
}
