@import "../vars";
.m-btn {
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all ease-out .225s;
    padding: 0;
    &:focus {
        box-shadow: none;
    }
}

.m-btn-primary{
    border-radius: 3.125em;
    background-image: $gradient;
    color: #000;
    min-width: 22.875em;
    height: 3.75em;
    font-weight: 500;
    position: relative;
    padding: 0 3em;
    &:hover{
        background-size: 200%;
        color: #000;
    }
    span{
        font-size: 1em;
        line-height: 120%;
    }
    img{
        width: 1em;
        position: absolute;
        right: 1.25em;
        top: 50%;
        transform: translateY(-50%);
    }
}


@media screen and (max-width: 576px){
    .m-btn-primary{
        width: 100%;
        min-width: auto;
    }
}
